document.addEventListener('DOMContentLoaded', function(){
    iniciarApp();
});

function iniciarApp() {
    navegacionFija(); // Detecta cuando scroll ha dado
    crearGaleria();
    scrollNav(); // Realiza el scroll smooth
}

function navegacionFija() {
    const barra = document.querySelector('.header');
    const sobreFestival = document.querySelector('.sobre-festival');
    const body = document.querySelector('body');

    window.addEventListener('scroll', function() {
        console.log(sobreFestival.getBoundingClientRect() );

        if(sobreFestival.getBoundingClientRect().bottom < 0) {
            // console.log("Ya pasamos el elemento");
            barra.classList.add('fijo');
            body.classList.add('body-scroll');
        }else {
            barra.classList.remove('fijo');
            body.classList.remove('body-scroll');
        }

    });
}

function scrollNav() {
    // lo primero que se hace es seleccionar los enlaces con la siguiente linea
    const enlaces = document.querySelectorAll('.navegacion-principal a');
    enlaces.forEach( enlace => {
        enlace.addEventListener('click', function(e) {
            e.preventDefault(); //previene el llevar de golpe al link seleccionado
            // console.log(e.target.attributes.href.value);
            const seccionScroll = e.target.attributes.href.value;
            const seccion = document.querySelector(seccionScroll);
            seccion.scrollIntoView({ behavior: "smooth" });
        });
    });
}

function crearGaleria() {
    const galeria = document.querySelector('.galeria-imagenes'); //en esta linea se define en donde se inyecta la galeria y es justo en la clase de html
    // galeria.textContent = 'Vamos a crear la galeria';
    for(let i = 1; i <= 12; i++) {
        const imagen = document.createElement('picture'); //crea codigo HTML de picture
        // el acento invertido o grave se logra con teclado en ingles y tecla al lado del 1
        // este codigo genera cada imagen que pasa por el ciclo for
        imagen.innerHTML = `
            <source srcset="build/img/thumb/${i}.avif" type="image/avif">
            <source srcset="build/img/thumb/${i}.webp" type="image/webp">
            <img loading="lazy" width="200" height="300" src="build/img/thumb/${i}.jpg" alt="imagen galeria">
            `;

            // Se crea la funcion para que este escuchando por click en imagen
            imagen.onclick = function() {
                mostrarImagen(i);
            }

        // console.log(imagen);
        galeria.appendChild(imagen);
    }
}

function mostrarImagen(id) {
    // console.log('mostrando....', id);
    const imagen = document.createElement('picture'); 
    imagen.innerHTML = `
            <source srcset="build/img/grande/${id}.avif" type="image/avif">
            <source srcset="build/img/grande/${id}.webp" type="image/webp">
            <img loading="lazy" width="200" height="300" src="build/img/grande/${id}.jpg" alt="imagen galeria">
            `;

            // Crea el overlay con la imagen
            const overlay = document.createElement('DIV');
            overlay.appendChild(imagen);
            overlay.classList.add('overlay'); //crea nueva clase cuando se clickea imagen
            overlay.onclick = function() {//esta funcion cierra la imagen dando click donde sea no solo en la X
                const body = document.querySelector('body');
                body.classList.remove('fijar-body'); //elimina la clase que bloquea el scroll
                overlay.remove(); // elimina la proyeccion de imagen
            }

            // Boton para cerrar la ventana MODAL
            const cerrarModal = document.createElement('P');
            cerrarModal.textContent = 'X'; //imprime la X
            cerrarModal.classList.add('btn-cerrar');//crea clase
            // La siguiente funcion elimina overlay
            cerrarModal.onclick = function() {
                const body = document.querySelector('body');
                body.classList.remove('fijar-body'); //elimina la clase que bloquea el scroll
                overlay.remove(); // elimina la proyeccion de imagen
            }
            overlay.appendChild(cerrarModal);

            //Lo a√±ade al HTML
            const body = document.querySelector('body'); //selecciona la etiqueta de body para que se imprima la imagen
            body.appendChild(overlay);
            body.classList.add('fijar-body'); //llama clase dada de alta en globales

}